.product-page{
    margin-bottom: 150px;
}

.panel{
    display: flex;
    gap:10px;
}
.panel>.image-holder{
width:100%;
height:600px;
object-fit: cover;
border-radius:6px;
}
.button-holder{
    height: 100px;
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.productpage-menu{
    width:60%;
}

.productpage-menu .title{
    font-weight:700;
    font-size:2rem;
}
.productpage-menu .price{
    color:#ab7a5f;
    margin:10px 0px;
    font-weight:600;
}
.productpage-menu .description{
    margin-bottom:30px;
}
.productpage-menu .inline{
    margin-bottom:10px;
    display: flex;
    justify-content:space-between;
}

.productpage-counter{
    width:100%;
    margin:30px 0px;
}

.productpage-counter span{
    font-size:3rem;
    padding:0px 20px;
    color:#ab7a5f;
}

.productpage-counter img{
    width:40px;
    cursor: pointer;
}

@media screen and (max-width:1000px) {
    .product-page .wrapper{
        width:80%;
    }


}